@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  --bg: #e0e0e0;
  --main: #202020;
  --secondary: #161616;
  --main-light: #2020202e;
  --shadow-1: #0d0d0d;
  --shadow-2: #333333;
  --gradient-1: #222222;
  --gradient-2: #1d1d1d;
  --text: #ffffff;
  --bar: #cccccc;
}


/* width */
.tableScroll::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.tableScroll::-webkit-scrollbar-track {
  background: #888;
  border-radius: 0 0.75rem 0.75rem 0;
}

/* Handle */
.tableScroll::-webkit-scrollbar-thumb {
  background: rgb(55 65 81 / 1);
  border-radius: 0 0.75rem 0.75rem 0;
}

/* Handle on hover */
.tableScroll::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.tableScroll table tbody tr:nth-child(even) {
  background-color: rgb(55 65 81 / 0.1) !important;
}

.stroke-solid {
  stroke-dashoffset: 0;
  stroke-dashArray: 300;
  stroke-width: 4px;
  transition: stroke-dashoffset 1s ease,
    opacity 1s ease;
}

.icon {
  transform: scale(.8);
  transform-origin: 50% 50%;
  transition: transform 200ms ease-out;
}

#play:hover {
  .stroke-solid {
    opacity: 1;
    stroke-dashoffset: 300;
    stroke: #2DD4BF;
  }

  .icon {
    transform: scale(.9);
  }
}

svg#play {
  width: 35px;
  display: block;
  cursor: pointer;
  transform-origin: 50% 50%;
}